<template>
  <v-app>
    <v-app-bar
      v-if="logged"
      app
      color="primary"
      dark
      >
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title>Visionet</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon @click="$router.push('/notification')">
        <v-icon>mdi-bell</v-icon>
      </v-btn>
      <v-btn icon @click="{}">
        <v-icon>mdi-logout</v-icon>
      </v-btn>
    </v-app-bar>
    <v-navigation-drawer
