<script>
import {
  VApp, 
  VBtn, 
  VAppBar, 
  VAppBarNavIcon, 
  VToolbarTitle, 
  VSpacer, 
  VIcon, 
  VNavigationDrawer, 
  VSystemBar, 
  VList, 
  VListItem, 
  VListItemAvatar, 
  VImg, 
  VListItemGroup,
  VListItemIcon,
  VListItemContent,
  VListItemTitle} from 'vuetify/lib'
export default {
  name: 'App',
  components: {
    VApp, 
    VBtn, 
    VAppBar, 
    VAppBarNavIcon, 
    VToolbarTitle, 
    VSpacer, 
    VIcon, 
    VNavigationDrawer, 
    VSystemBar, 
    VList, 
    VListItem, 
    VListItemAvatar, 
    VImg, 
    VListItemGroup,
    VListItemIcon,
    VListItemContent,
    VListItemTitle
  },
